import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import {DecimalPipe} from '@angular/common';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { Login } from '../providers/login';
import { MapPage } from '../pages/map-page/map-page';
import { Historical } from '../pages/historical/historical';
import { RealTime } from '../pages/real-time/real-time';
import { GroupsPage } from '../pages/groups-page/groups-page';
import { HistoricalForm } from '../pages/historical-form/historical-form';
import { PathsList } from '../pages/paths-list/paths-list';
import { GeocodingService } from '../utils/geocoding.service';
import { MapService } from '../utils/map.service';
import {RealTimeService} from '../providers/real-time.service';
import {HistoricalService} from '../providers/historical.service';
import { DataManagementService } from '../providers/data-management.service';
import { IonicStorageModule } from '@ionic/storage';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MapPage,
    Historical,
    RealTime,
    GroupsPage,
    HistoricalForm,
    PathsList
  ], 

  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MapPage,
    Historical,
    RealTime,
    GroupsPage,
    HistoricalForm,
    PathsList
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Login,
    GeocodingService,
    MapService,
    RealTimeService,
    DataManagementService,
    HistoricalService,
    DecimalPipe,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    IonicStorageModule
  ]
})
export class AppModule {}



