import { Component } from '@angular/core';
import { NavController, NavParams, ModalController,LoadingController,ViewController } from 'ionic-angular';


@Component({
  selector: 'page-paths-list',
  templateUrl: 'paths-list.html',
})
export class PathsList {

  paths: any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
    this.paths = navParams.get('paths');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PathsList');
  }

  drawPath(path: any){
    this.viewCtrl.dismiss(path);
  }

  closeModal(){
    this.viewCtrl.dismiss(null);
  }

}



