import { Component } from '@angular/core';
import { NavController, NavParams, ToastController,ModalController,ViewController  } from 'ionic-angular';
import { RealTimeService } from '../../providers/real-time.service';
import { Subscription } from "rxjs";


@Component({
  selector: 'page-groups-page',
  templateUrl: 'groups-page.html',
})
export class GroupsPage {
  allGroups: Subscription;
  groups: any[];
  selectedDevice: number;
  searchWord : string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public realTimeService: RealTimeService) {
    this.groups = navParams.get('groups');
    //this.groups = this.processGroups(this.groups);
  }

 /* processGroups(groups: any){
    groups.forEach(group => {
      
    });
    return groups;
  }*/

  closeModal(){
    this.viewCtrl.dismiss(this.selectedDevice);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GroupsPage');
  }

  searchGroup(){
this.allGroups = this.realTimeService.getAllGroups(this.searchWord).subscribe(groupes => {
      this.groups = groupes;
      console.log(this.groups);
    });
  }

  goToRealTimeRecord(deviceId: number){
    this.selectedDevice = deviceId;
    this.viewCtrl.dismiss(this.selectedDevice);
  }

  startEngine(){
    console.log("startEngine");
  }

  stopEngine(){
    console.log("stopEngine");
  }

  displayTodaysMileage(){
    console.log("displayTodaysMileage");
  }

  displayCurrentPath(){
    console.log("displayCurrentPath");
  }

}



