import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {Http, Headers} from '@angular/http';
import {dns} from "./global.config";
import {RealTimeRecord, Group} from "../objects/real-time";
import {Poi} from "../objects/poi";

@Injectable()
export class RealTimeService {

    token: string;

    constructor(private _http: Http) {

    }

    getRealTimeRecord(deviceId: number): Observable<RealTimeRecord> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.get(dns + 'realTimeRecords/' + deviceId, {headers: headers}).map(res => res.json());
    }

    getAllPois(): Observable<Poi[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.get(dns + 'pointInterests/dtos', {headers: headers}).map(res => res.json());
    }

    getAllRealTimeRecords(): Observable<RealTimeRecord[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.get(dns + 'realTimeRecords', {headers: headers}).map(res => res.json());
    }

    getAllGroups(keyword: string): Observable<Group[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.get(dns + 'groupes/details?keyword=' + keyword, {headers: headers}).map(res => res.json());
    }

    getCurrentPath(deviceId: number): Observable<any> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.post(dns + 'paths/currentPath/' + deviceId,null,  {headers: headers}).map(res => res.json());
    }

}


