import {Driver} from "./data-management";

export class RealTimeRecord {
    
        idRealTimeRecord: number;
        recordTime: Date;
        coordinate: {lat: number,lng: number};
        speed: number;
        temperature: string;
        validity: boolean;
        ignition: boolean;
        power: number;
        mems_x: number;
        mems_y: number;
        mems_z: number;
        sendFlag: number;
        type:'AA'|'GPRMC';
        satInView: number;
        signal: string;
        heading: number;
        matricule: string;
        mark: string;
        realTimeRecordStatus: string;
        geocoding: string;
        geocodingDetails: string;
        relativePosition: string;
        rotationAngle: number;
        icon : string;
    
        vehicule: Vehicule = null;
    
        constructor() {
            this.geocoding = `Chargement..`;
            this.geocodingDetails = 'Chargement..';
            this.speed = 0;
            this.recordTime = new Date();
            this.coordinate = {lat: 0, lng: 0};
            this.realTimeRecordStatus = '';
            this.rotationAngle= 0;
        }
    
    }
    
    export class Group {
        nom: string;
        isOpen: boolean;
        vehicules: Vehicule[];
         icon: string;
    }
    
    export class Vehicule {
        idDevice: number;
        matricule: string;
        mark: string;
        recordTime: Date;
        coordinate: {lat: number,lng: number};
        geocoding: string;
        geocodingDetails: string;
        speed: number;
        driverName: string;
        driver: Driver;
    
        realTimeRecord: RealTimeRecord;
    
        constructor() {
            this.realTimeRecord = new RealTimeRecord();
        }
    }


