import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {Http, Headers} from '@angular/http';
import {dns} from "./global.config";

@Injectable()
export class HistoricalService {

    token: string;

    constructor(private _http: Http) {

    }

    getAllGroups(): Observable<any[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.get(dns + 'groupes/minify', {headers: headers}).map(res => res.json());
    }

    getAllPaths(deviceId: number, dateInterval: any): Observable<any[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.post(dns + 'paths/' + deviceId, dateInterval, {headers: headers}).map(res => res.json());
    }

    getCurrentPath(deviceId: number): Observable<any> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.post(dns + 'paths/currentPath/' + deviceId,null,  {headers: headers}).map(res => res.json());
    }

    getPathDetails(deviceId: number, dateInterval: any): Observable<any> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.token);
        return this._http.post(dns + 'paths/details/' + deviceId, dateInterval, {headers: headers}).map(res => res.json());
    }

}



