import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, ViewController,LoadingController } from 'ionic-angular';
import { HistoricalService } from '../../providers/historical.service';
import { Subscription } from "rxjs";


@Component({
  selector: 'page-historical-form',
  templateUrl: 'historical-form.html',
})
export class HistoricalForm {

  allGroups: Subscription;
  selectedDeviceId: any;
  groups: any[];
  selectedGroup: any;
  vehicules: any;
  firstDate: any;
  lastDate: any;

  selectedGroupId: any;

  loader: any;

  constructor(private historicalService: HistoricalService, public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController) {
    this.init();
  }

  ionViewWillEnter() {
    
  }

  init() {
    this.loader = this.loadingCtrl.create({
      content: "Veuillez attendre..."
    });
    this.loader.present();
    this.loadGroups();
    
  }


  ionViewDidLoad() {

  }

  loadGroups() {
    this.allGroups = this.historicalService.getAllGroups().subscribe(groupes => {
      this.groups = groupes;
      this.selectedGroup = this.groups[0];
      this.vehicules = this.selectedGroup.vehicules;

      if (this.vehicules.length > 0 && this.vehicules) {
        this.selectedDeviceId = this.vehicules[0].idDevice;
        
      }
      /*this.groups.forEach(group => {
        if(group.vehicules.length == 0 && this.groups != null){
          this.groups.splice(this.groups.indexOf(group));
        }
      });*/
      this.loader.dismiss();
    });
  }

  chooseGroup(group) {
    this.selectedGroup = group;
    this.selectedGroupId = group;
    this.groups.forEach(gr => {
      if(gr.idGroupe == group){
        this.vehicules = gr.vehicules;
      }
    })
    if (this.vehicules.length > 0 && this.vehicules) {
      this.selectedDeviceId = this.vehicules[0].idDevice;
    }
  }

  chooseVehicule(vehiculeId) {
    this.selectedDeviceId = vehiculeId;
  }

  getAllPaths() {
    var date = new Date();  
    this.firstDate = new Date(date.getFullYear(), date.getMonth(), date.getDate(), 0, 0, 0);
    this.lastDate = new Date(date.getFullYear(), date.getMonth(), date.getDate(), 23, 59, 59);
    this.viewCtrl.dismiss({ selectedDeviceId: this.selectedDeviceId, firstDate: this.firstDate, lastDate: this.lastDate });
  }

  closeModal(){
   this.viewCtrl.dismiss(null);

  }
  c
}



